.site-nav {
  display: flex;
  padding-left: 10px;
  margin-top: 0;
  margin-bottom: 0;
  gap: 20px;

  @include vp-1199 {
    gap: 10px;
  }

  @include vp-767 {
    display: contents;
  }
}

.site-nav li {
  list-style-type: none;
  width: 50%;
}

.site-nav a {
  display: block;
  @include text-roboto-14-16-400;
  color: $color-text-white;
  text-decoration: none;
  position: relative;
  padding: 5px 10px;
  outline: none;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 10px;
    height: 100%;
    width: calc(100% - 20px);
  }

  @include hover {
    &::after {
      border-bottom: 1px solid white;
    }
  }

  @include focus {
    &::before {
      border: 1px solid white;
    }
  }

  @include active {
    &::before,
    &::after {
      border: none;
    }
  }

  @include vp-767 {
    @include text-roboto-16-18-400;
    padding: 5px 0;
  }
}

.site-nav--disabled a {
  opacity: 0.4;
}
